  color: #ba0c2f;
}

.gform_wrapper {
  ul, li {
    list-style: none;
    margin: 0;
  }
}

.gfield_label, h2.gsection_title {
  font-size: 22px;
  color: $primary-color;
  padding: 5px 0;
