.gform_page_footer {
  margin-top: 30px;
}

::-webkit-input-placeholder, :-moz-placeholder, ::-moz-placeholder, :-ms-input-placeholder {
  color: #ccc;
}

.gform_wrapper input {
  &[type=text], &[type=url], &[type=email], &[type=tel], &[type=number], &[type=password] {
    font-size: 16px !important;
    padding-left: 7px !important;
    height: $field-height;
    border: $field-border;
  }
}

select, textarea {
	font-size: 16px !important;
  	padding-left: 7px !important;
    border: solid 2px #848484;
    height: $field-height;
    color: #848484;
}

select {
	background: url(../images/select-arrows.png) 98% center no-repeat;
	background-size: 12px 21px;
}

[type="radio"]:not(:checked) + label + input {
	display: none;
}

input[name*="other"] {
  margin-top: 10px;
  width: 50%;
}

img {
  &.add_list_item {
    background-image: url(data:image/svg+xml;base64,);
  }
  &.delete_list_item {
    background-image: url(data:image/svg+xml;base64,);
  }
  &.add_list_item, &.delete_list_item {
    width: 1em;
    height: 1em;
    background-size: 1em 1em;
    opacity: 0.5;
  }
  &.add_list_item {
    &:hover, &:active {
      opacity: 1.0;
    }
  }
  &.delete_list_item {
    &:hover, &:active {
      opacity: 1.0;
    }
  }
}

