  padding: 15px; }
  .woocommerce .sale a.button.alt,
  .woocommerce .sale button.button.alt,
  .woocommerce .sale input.button.alt {
    background: #595959;
    padding: 10px; }
    .woocommerce .sale a.button.alt:hover,
    .woocommerce .sale button.button.alt:hover,
    .woocommerce .sale input.button.alt:hover {
      background: #333; }

.woocommerce table.variations tr select {
  width: 3.631em;
  text-align: center;
  padding: 0px;
  height: 37px !important; }

.woocommerce .comment-respond .form-submit input#submit {
  padding: 12px;
  border: 2px solid #cacaca;
  border-radius: 0px;
  line-height: normal;
  height: 44px;
  display: block;
  transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
  background-color: #595959;
  color: #fefefe !important; }
  .woocommerce .comment-respond .form-submit input#submit:hover {
    background: #333; }

.woocommerce form.edit-account legend {
  border: 0;
  padding: 10px;
  color: #aaa !important;
  font-weight: 600; }

.woocommerce-cart table.cart td.actions .coupon .input-text {
  float: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid #d3ced2;
  padding: 6px 6px 5px;
  margin: 0 4px 0 0;
  outline: 0;
  line-height: 1;
  width: 150px; }

.view-cart-container {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  font-weight: 600;
  border-right: solid 1px #fff; }
  @media screen and (min-width: 40em) {
    .view-cart-container {
      margin-top: 10px; } }
  @media screen and (max-width: 39.9375em) {
    .view-cart-container {
      width: 60px;
      height: 64px; } }
  .view-cart-container svg.icon {
    fill: #fefefe;
    width: 60px;
    padding: 7px 10px; }
    @media screen and (min-width: 40em) {
      .view-cart-container svg.icon {
        fill: #666; } }
  .view-cart-container a.view-cart-icon {
    color: white;
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
  .view-cart-container span {
    display: block;
    margin-top: 12px;
    position: absolute;
    left: 50%;
    font-weight: 700; }
    @media screen and (max-width: 39.9375em) {
      .view-cart-container span {
        margin-top: 0;
        top: 20%;
        color: #666; } }
